import React from "react";
import slider1 from "../../Assets/images/10.jpg";
import slider2 from "../../Assets/images/9.jpg";
import slider3 from "../../Assets/images/14.jpg";
import img2 from "../../Assets/images/15.jpg";
import img1 from "../../Assets/images/1.jpg";
import Slider from "react-slick";
import { useTranslation } from 'react-i18next';
import "./MainSlider.module.css";
import useLocalStorage from "use-local-storage";
export default function MainSlider() {

  const preference = window.matchMedia("(prefers-color-scheme: dark)").matches;
  const [isDark, setIsDark] = useLocalStorage("isDark", preference);
  var settings = {
    dots: false,
    autoplay: true,
    autoplayspeed: 1000,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false
  };

  
  const { t, i18n } = useTranslation();
  const changeEN =() => {
      i18n.changeLanguage('en')
  }
  const changeAR =() => {
      i18n.changeLanguage('ar')
  }
  return (
    <>
    <section className="slider-wrapper App " data-theme={isDark ? "dark" : "light"}>
    
    
    <div className="row  gx-0">
        <div className="col-md-12 ">
          <Slider {...settings}  className="slider-bottom">
          <div className="slider-item itemz   d-flex align-items-center justify-content-center text-center">
          <div className="text-white">
         
        </div>
        <div className="child1 position-absolute top-50 text-white">
          <h1></h1>
        </div>
         
          </div>
          <div className="img ">
          <div className="slider-item itemp  d-flex align-items-center justify-content-center text-center">
          <div className="text-white">
         
        </div>
        <div className="child1 position-absolute top-50 text-white">
          <h1></h1>
        </div>
         
          </div>          
          </div>
          <div className="img ">
          <div className="slider-item ite  d-flex align-items-center justify-content-center text-center">
          <div className="text-white">
         
        </div>
        <div className="child1 position-absolute top-50 text-white">
          <h1></h1>
        </div>
         
          </div>          
          </div>
          
          </Slider>
          </div>
          
          </div>
          </section>
    </>
  );
}
