import React from 'react'
import { Link } from 'react-router-dom'
import Meadia from './../Meadia/Meadia';
import useLocalStorage from "use-local-storage";
import { useTranslation } from 'react-i18next';
import "./AccordionItem.module.css";
import  { Fragment, useState } from "react";
export default function AccordionItem() {

  
  const { t, i18n } = useTranslation();
  const changeEN =() => {
      i18n.changeLanguage('en')
  }
  const changeAR =() => {
      i18n.changeLanguage('ar')
  }


  const isRTL = i18n.language === 'ar'; 
  const textDirection = isRTL ? 'rtl' : 'ltr';

  const preference = window.matchMedia("(prefers-color-scheme: dark)").matches;
  const [isDark, setIsDark] = useLocalStorage("isDark", preference);

  return (
    <>
    
    <div style={{ direction: textDirection }} className="card-wrapper App content-cc" data-theme={isDark ? "dark" : "light"}>
      <div className="faq-card ">
        <main className="faq-content ">
          <h2 className=" mo">FAQ</h2>
          <div className="faq-articles">
            <div className="faq-accordion">
              <input type="checkbox" className="tgg-title" id="tgg-title-1" />
              <div   className="faq-accordion-title">
                <label htmlFor="tgg-title-1" >
                  <span  className=" accordion-title text-white">How many team members can I invite?</span>
                  <span className="arrow-icon">
                    <img src="https://raw.githubusercontent.com/Romerof/FAQ-accordion-card/main/images/icon-arrow-down.svg" alt="Arrow Icon" />
                  </span>
                </label>
              </div>
              <div style={{ direction: textDirection }}  className="faq-accordion-content">
                <p className=" text-warning">You can invite up to 2 additional users on the Free plan. There is no limit on team members for the Premium plan.</p>
              </div>
            </div>
            <div style={{ direction: textDirection }}  className="faq-accordion">
              <input type="checkbox" className="tgg-title" id="tgg-title-2" />
              <div className="faq-accordion-title">
                <label htmlFor="tgg-title-2">
                  <span className="accordion-title text-white">What is the maximum file upload size?</span>
                  <span className="arrow-icon">
                    <img src="https://raw.githubusercontent.com/Romerof/FAQ-accordion-card/main/images/icon-arrow-down.svg" alt="Arrow Icon" />
                  </span>
                </label>
              </div>
              <div style={{ direction: textDirection }}  className="faq-accordion-content">
                <p className=" text-warning">No more than 2GB. All files in your account must fit your allotted storage space.</p>
              </div>
            </div>
            <div className="faq-accordion">
              <input type="checkbox" className="tgg-title" id="tgg-title-3" />
              <div style={{ direction: textDirection }}  className="faq-accordion-title">
                <label htmlFor="tgg-title-3">
                  <span className="accordion-title text-white">What is the Moaz file upload size?</span>
                  <span className="arrow-icon">
                    <img src="https://raw.githubusercontent.com/Romerof/FAQ-accordion-card/main/images/icon-arrow-down.svg" alt="Arrow Icon" />
                  </span>
                </label>
              </div>
              <div style={{ direction: textDirection }}  className="faq-accordion-content">
                <p className=" text-warning">No more than 2GB.zozozoozozozozo All files in your account must fit your allotted storage space.</p>
              </div>
            </div>
          </div>
          <div style={{ direction: textDirection }}  className="py-3 ">
          <Link to={'/Meadia'} className="botton bdevs-el-btn ">
          See More
          </Link>
          </div>
        </main>
      </div>
    </div>
      
      
    </>
  )
}
