import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import Testi18n from '../../Testi18n';
import Toggle from '../Toggle/Toggle';
import useLocalStorage from "use-local-storage";
import logo from "../../Assets/images/Inklinic.jpg";
import "./NavBar.css";

export default function NavBar() {
  let location = useLocation();
  const { t, i18n } = useTranslation();
  const preference = window.matchMedia("(prefers-color-scheme: dark)").matches;
  const [isDark, setIsDark] = useLocalStorage("isDark", preference);
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const changeEN = () => {
    i18n.changeLanguage('en');
  };

  const changeAR = () => {
    i18n.changeLanguage('ar');
  };
  const isRTL = i18n.language === 'ar'; 
  const textDirection = isRTL ? 'rtl' : 'ltr';

  const openSidebar = () => {
    setSidebarOpen(true);
  };

  const closeSidebar = () => {
    setSidebarOpen(false);
  };

  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-light App SignOut lol" data-theme={isDark ? "dark" : "light"}>
        <div className="container">
          <Link className="navbar-brand SignOut" to="/">
            <img src={logo} height={65} className="w-100 rounded-circle" alt="Inklinic" />
          </Link>
          <button className="navbar-toggler openbtn" type="button" onClick={openSidebar}>
            <span></span>
          </button>
          <div className="collapse navbar-collapse">
            <ul className="navbar-nav m-md-auto mb-2 mb-lg-0">
              <li className="nav-item py-2">
                <Link className={`path-Link ${location.pathname === '/' ? 'rating-color' : ''} SignOut ${i18n.language === 'ar' ? 'arabic-text' : ''}`} aria-current="page" to="/">
                  {t('Home')}
                </Link>
              </li>
              <li className="nav-item py-2">
                <Link className={`path-Link ${location.pathname === '/about' ? 'rating-color' : ''} SignOut`} aria-current="page" to="/about">
                  {t('About')}
                </Link>
              </li>
              <li className="nav-item py-2">
                <Link className={`path-Link ${location.pathname === '/categories' ? 'rating-color' : ''} SignOut`} aria-current="page" to="/categories">
                  {t('Categories')}
                </Link>
              </li>
              <li className="nav-item py-2">
                <Link className={`path-Link ${location.pathname === '/meadia' ? 'rating-color' : ''} SignOut`} aria-current="page" to="/meadia">
                  {t('Media')}
                </Link>
              </li>
              <li className="nav-item py-2">
                <Link className={`path-Link ${location.pathname === '/contact' ? 'rating-color' : ''} SignOut`} aria-current="page" to="/contact">
                  {t('Contact')}
                </Link>
              </li>
              <li>
                <Link>
                  <Testi18n />
                </Link>
              </li>
              <Toggle isChecked={isDark} handleChange={() => setIsDark(!isDark)} />
            </ul>
          </div>
        </div>
      </nav>
      <div className={`sidebar ${sidebarOpen ? 'open' : ''}`} style={{ width: sidebarOpen ? '250px' : '0' }}>
        <a href="javascript:void(0)" className="closebtn" onClick={closeSidebar}>&times;</a>
        <div style={{ direction: textDirection }} className="sidebar-content">
          <ul className="navbar-nav m-md-auto mb-2 mb-lg-0">
            <li className="nav-item py-2">
              <Link className={`path-Link ${location.pathname === '/' ? 'rating-color' : ''} SignOut ${i18n.language === 'ar' ? 'arabic-text' : ''}`} aria-current="page" to="/" onClick={closeSidebar}>
                {t('Home')}
              </Link>
            </li>
            <li className="nav-item py-2">
              <Link className={`path-Link ${location.pathname === '/about' ? 'rating-color' : ''} SignOut`} aria-current="page" to="/about" onClick={closeSidebar}>
                {t('About')}
              </Link>
            </li>
            <li className="nav-item py-2">
              <Link className={`path-Link ${location.pathname === '/categories' ? 'rating-color' : ''} SignOut`} aria-current="page" to="/categories" onClick={closeSidebar}>
                {t('Categories')}
              </Link>
            </li>
            <li className="nav-item py-2">
              <Link className={`path-Link ${location.pathname === '/meadia' ? 'rating-color' : ''} SignOut`} aria-current="page" to="/meadia" onClick={closeSidebar}>
                {t('Media')}
              </Link>
            </li>
            <li className="nav-item py-2">
              <Link className={`path-Link ${location.pathname === '/contact' ? 'rating-color' : ''} SignOut`} aria-current="page" to="/contact" onClick={closeSidebar}>
                {t('Contact')}
              </Link>
            </li>
            <li>
              <Link>
                <Testi18n />
              </Link>
            </li>
            <Toggle isChecked={isDark} handleChange={() => setIsDark(!isDark)} />
          </ul>
        </div>
      </div>
    </>
  );
}
