import React, { Fragment, useState } from "react";
import { useTranslation } from 'react-i18next';

const Testi18n = () => {
    const { t, i18n } = useTranslation();
    const isRTL = i18n.language === 'ar';
    const textDirection = isRTL ? 'rtl' : 'ltr';

    const [isOpen, setIsOpen] = useState(false);
    const [selectedOption, setSelectedOption] = useState('<i class="fa-solid fa-globe"></i>');

    const options = ['En', 'Ar'];

    const toggleDropdown = (event) => {
        event.preventDefault();
        setIsOpen(!isOpen);
    };

    const handleOptionClick = (option) => {
        setSelectedOption(option);
        setIsOpen(false);
        if (option === 'En') {
            i18n.changeLanguage('en');
        } else if (option === 'Ar') {
            i18n.changeLanguage('ar');
        }
    };

    return (
        <div style={{ direction: textDirection }}>
            <div className="dropdown text-center" style={{ width: '50px' }}>
                <div className="select text-center" onClick={toggleDropdown}>
                    <span className="selected text-center" dangerouslySetInnerHTML={{ __html: selectedOption }}></span>
                    <div className={`caret ${isOpen ? 'caret-rotate' : ''}`}></div>
                </div>
                <ul className={`menu ${isOpen ? 'menu-open' : ''} text-center`}>
                    {options.map((option, index) => (
                        <li key={index} onClick={() => handleOptionClick(option)} className="text-white text-center">{option}</li>
                    ))}
                </ul>
            </div>
        </div>
    );
};

export default Testi18n;
